import React from "react";
import "./styles/topnav.css";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function TopNav(match) {
  return (
    <header className="topnav">
      <Link style={{ textDecoration: "none", color: "#334141" }} >
        <p onClick={() => console.log("working")}>Welcome to Hubmart Stores Limited </p>
      </Link>
      <Link style={{ textDecoration: "none", color: "#334141" }} >
        <p onClick={() => console.log("working")}>
          <span style={{ paddingRight: "6px" }}>
            <FontAwesomeIcon icon="clock" />
          </span>
          Same Day delivery for orders made from 10am – 3pm
        </p>
      </Link>
      <Link style={{ textDecoration: "none", color: "#334141" }} >
        <p onClick={() => console.log("working")}>
          <span style={{ paddingRight: "6px" }}>
            <FontAwesomeIcon icon="map-marker-alt" />
          </span>
          Store Locator
        </p>
      </Link>
      <Link style={{ textDecoration: "none", color: "#334141" }} to='/track-your-order'>
        <p onClick={() => console.log("working")}>
          <span style={{ paddingRight: "6px" }}>
            <FontAwesomeIcon icon="truck" />
          </span>
          Track Your Order
        </p>
      </Link>
      <Link style={{ textDecoration: "none", color: "#334141" }} to="/shop">
        <p onClick={() => console.log("working")}>
          <span style={{ paddingRight: "6px" }}>
            <FontAwesomeIcon icon="cart-arrow-down" />
          </span>
          Shop
        </p>
      </Link>
      <Link style={{ textDecoration: "none", color: "#334141" }} to="/my-account">
        <p>
          <span style={{ paddingRight: "6px" }}>
            <FontAwesomeIcon icon="user" />
          </span>
          My Account
        </p>
      </Link>
    </header>
  );
}

export default TopNav;
