import React from 'react';
import {
    Link, 
    NavLink, 
    Switch,
    Route,
} from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import './styles/dashboard.css';
import OrderView from './OrderView';
import Billing from './Billing';
import Shipping from './Shipping';
import AccountView from './AccountView';
import MyAccount from './MyAccount';
import MainApp from '../main/MainApp';
import Download from './Download';
import PaymentDetails from './PaymentDetails';
import Address from './Address';


export default function DashBoard({match,props}) {
    // let query = useParams();
    return (
        <>
                    <div className='breadcrumbs'>
                        <p>Home <span>&gt;</span></p>
                        <p>{match.url} &gt;</p>
                    </div>
                        <h3>My Account</h3>
                        <div className='account'>

                            <div className='account-links'>
                                <div style={{textDecoration:"none", borderBottom:'.1rem rgb(216, 213, 213) solid'}}>
                                    <h2>My Account</h2>
                                </div>
                                <div className='account-link'>
                                    <ul>
                                        <li>
                                            <NavLink 
                                                style={{textDecoration:"none",color:'#000000'}} exact to={`${match.url}`}
                                                activeStyle={{textDecoration:"none",color:'#F15A22', marginLeft: '-1rem'}}
                                                >
                                                Dashboard
                                                <span style={{float:'right'}}><FontAwesomeIcon icon='tachometer-alt'/></span>
                                            </NavLink>
                                        </li>
                                        <li>
                                            <NavLink 
                                            style={{textDecoration:"none",color:'#000000'}} to={`${match.url}/orders`}
                                            activeStyle={{textDecoration:"none",color:'#F15A22', marginLeft: '-1rem'}}
                                            >
                                                Orders
                                                <span style={{float:'right'}}><FontAwesomeIcon icon='shopping-basket'/></span>
                                            </NavLink>
                                        </li>
                                        <li>
                                            <NavLink 
                                                style={{textDecoration:"none",color:'#000000'}} to={`${match.url}/downloads`}
                                                activeStyle={{textDecoration:"none",color:'#F15A22', marginLeft: '-1rem'}}
                                            >
                                                Downloads
                                                <span style={{float:'right'}}><FontAwesomeIcon icon='download'/></span>
                                            </NavLink>
                                        </li>
                                       <li>
                                            <NavLink 
                                                style={{textDecoration:"none",color:'#000000'}} to={`${match.url}/address`}
                                                activeStyle={{textDecoration:"none",color:'#F15A22', marginLeft: '-1rem'}}
                                            >
                                                Addresses
                                                <span style={{float:'right'}}><FontAwesomeIcon icon='home'/></span>
                                            </NavLink>
                                        </li>
                                        <li>
                                            <NavLink  
                                                style={{textDecoration:"none",color:'#000000'}} to={`${match.url}/payment-method`}
                                                activeStyle={{textDecoration:"none",color:'#F15A22', marginLeft: '-1rem'}}
                                            >
                                                Payment methods
                                                <span style={{float:'right'}}><FontAwesomeIcon icon='credit-card'/></span>     
                                            </NavLink>
                                        </li>
                                        <li>
                                            <NavLink 
                                                style={{textDecoration:"none",color:'#000000'}} to={`${match.url}/account-details`}
                                                activeStyle={{textDecoration:"none",color:'#F15A22', marginLeft: '-1rem'}}
                                            >
                                                Account details
                                                <span style={{float:'right'}}><FontAwesomeIcon icon='user-alt'/></span>
                                            </NavLink>
                                        </li>
                                        <li>
                                            <NavLink 
                                                style={{textDecoration:"none",color:'#000000'}} to={`${match.url}/logout`}
                                                activeStyle={{textDecoration:"none",color:'#F15A22', marginLeft: '-1rem'}}
                                            >
                                                Logout
                                                <span style={{float:'right'}}><FontAwesomeIcon icon='sign-out-alt'/></span>
                                            </NavLink>
                                        </li>
                                       
                                    </ul>
                                </div>
                                
                            </div>
                            <div className='dashbord-view'>
                                <Switch>
                                    <Route path={`${match.path}/orders`}>
                                        <OrderView />
                                    </Route>
                                    <Route path={`${match.path}/account-details`}>
                                        <AccountView/>
                                    </Route>
                                    <Route path={`${match.path}/shipping`}>
                                        <Shipping />
                                    </Route>
                                    <Route path={`${match.path}/downloads`}>
                                        <Download />
                                    </Route>
                                    <Route path={`${match.path}/payment-method`}>
                                        <PaymentDetails />
                                    </Route>
                                    <Route path={`${match.path}/address`}>
                                        <Address {...match}/>
                                    </Route>
                                    <Route path={`${match.path}/logout`}>
                                        <MainApp />
                                    </Route>
                                    <Route path={`${match.path}/billing`}>
                                      <Billing />
                                    </Route>
                                    <Route path={`${match.path}/shipping`}>
                                      <Shipping />
                                    </Route>
                                     <Route path={`${match.path}`}>
                                        <MyAccount />
                                    </Route>
                                </Switch>
                            </div>
                        </div>
        </>
    )
}
 