import React from 'react'
import {Link} from 'react-router-dom';

export default function MyAccount(props) {
    return (
        <div>
            <p> Hello uipathtech (not uipathtech? <Link style={{textDecoration:"none",color:'#00aeff'}}>Logout</Link>)</p>
            <p>
                From your account dashboard you can view your <Link style={{textDecoration:"none",color:'#00aeff'}}>recent orders, </Link> manage your <Link style={{textDecoration:"none",color:'#00aeff'}}>shipping and billing addresses,</Link>
            </p>
            <p>
                and <Link style={{textDecoration:"none",color:'#00aeff'}}>edit your password and account details.</Link> 
            </p>
        </div>
    )
}
