import React from 'react'
import {connect}from 'react-redux';
import { createStructuredSelector } from 'reselect';
import Shopcart from '../../assets/images/Shopcart.png';
import { toggleCartHidden } from '../../reducers/actions/productActions';
import {selectCartItemsCount,selectCartSubTotal } from '../../reducers/productReducer/productSelector'

const Cart = ({toggleCartHidden,ItemCount,subtotal}) => {
    return (
        <div className='cart-logo' onClick={toggleCartHidden}>
                <img  src={Shopcart} />
                <span className='amount'>₦{subtotal}.00</span>
                <span className='count-product'>{ItemCount}</span>  
        </div>
    )
}
const mapStateToProps = createStructuredSelector({
    ItemCount: selectCartItemsCount,
    subtotal: selectCartSubTotal 
})
const mapDispatchToProps = dispatch =>({
    toggleCartHidden: () => dispatch(toggleCartHidden())
})
export default connect(mapStateToProps, mapDispatchToProps)(Cart)
