import { createSelector } from "reselect";

const selectCart = (state) => state.products;

export const selectCartItems = createSelector([selectCart], (products) => products.cartItems);

export const selectCartItemsCount = createSelector([selectCartItems], (cartItems) =>
  cartItems.reduce((accumulatedQty, cartItem) => accumulatedQty + cartItem.quantity, 0)
);

export const selectCartSubTotal = createSelector([selectCartItems], (cartItems) =>
  cartItems.reduce(
    (accumulatedQty, cartItem) => accumulatedQty + cartItem.quantity * cartItem.price,
    0
  )
);
