import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./styles/productDetails.css";
import cake from "../../assets/images/cake.jpg";
import RelatedProducts from "./RelatedProducts";
import { addCount, subCount } from "../../reducers/actions/productActions";

function Products({ addmore, addCount, subCount }) {
  return (
    <>
      <div className="products-breadcrumbs">
        <p>Home</p>
        <p>HubDeli</p>
        <p>Product1</p>
      </div>
      <div className="product-menu-item">
        <div className="sidemenu-item">
          <div className="show-catego">
            Show All Categories <span>&gt;</span>
          </div>
          <p className="slug">HubDeli</p>
          <div className="slug-item">
            <Link style={{ textDecoration: "none" }}>
              <p>Link 2</p>
            </Link>
            <Link style={{ textDecoration: "none" }}>
              <p>Link 2</p>
            </Link>
            <Link style={{ textDecoration: "none" }}>
              <p>Link 2</p>
            </Link>
            <Link style={{ textDecoration: "none" }}>
              <p>Link 2</p>
            </Link>
            <Link style={{ textDecoration: "none" }}>
              <p>Link 2</p>
            </Link>
            <Link style={{ textDecoration: "none" }}>
              <p>Link 2</p>
            </Link>
            <Link style={{ textDecoration: "none" }}>
              <p>Link 2</p>
            </Link>
          </div>
        </div>
        <div className="product-item-details">
          <img src={cake} alt="product" className="product-item-img" />
          <div className="product-item-properties">
            <p className="product-item-slug">HubDeli</p>
            <p className="product-item-name">HUBDELI CHICKEN SHAWARMA WITH SAUSAGE</p>
            <p className="compare-wishlist">
              {" "}
              Add to Wishlist <span>Compare</span>
            </p>
            <p className="product-item-price">₦1,500.00</p>
            <div style={{ display: "flex" }}>
              <div className="addmore">
                <input className="more-qty" placeholder={addmore} />
                <button className="add-plus" onClick={addCount}>
                  +
                </button>
                <button className="sub-minus" onClick={subCount}>
                  -
                </button>
              </div>
              <button className="product-addtocart">Add to Cart</button>
            </div>
          </div>
        </div>
      </div>
      <button className="review-link active" onClick={(event) => opentab(event, "reviews")}>
        Reviews
      </button>
      <div className="review-tab">
        <div
          id="reviews"
          className="review"
          style={{ display: "flex", justifyContent: "space-evenly", marginTop: "1rem" }}
        >
          <p>Based on 0 reviews</p>
          <p>Only logged in customers who have purchased this product may leave a review. </p>
        </div>
        <div className="review-stars">
          <p>
            0.0
            <p>overall</p>
          </p>
          <p>
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <span>---------5</span>
          </p>
          <p>
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <span>---------4</span>
          </p>
          <p>
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} /> <span>---------3</span>
          </p>
          <p>
            <FontAwesomeIcon icon={["fas", "star"]} />
            <FontAwesomeIcon icon={["fas", "star"]} />
            <span>---------2</span>
          </p>
          <p>
            <FontAwesomeIcon icon={["fas", "star"]} /> <span>---------1</span>
          </p>
        </div>
        <p className="each-reviews">There are no reviews yet.</p>
      </div>
      <RelatedProducts />
    </>
  );
}

const mapStateToProps = ({ products: { addmore } }) => ({
  addmore,
});
const mapDispatchToProps = (dispatch) => ({
  addCount: () => dispatch(addCount()),
  subCount: () => dispatch(subCount()),
});
// const mapDispatchToProps = ()
export default connect(mapStateToProps, mapDispatchToProps)(Products);
function opentab(evt, tabs) {
  // Declare all variables
  var i, review, reviewLink;

  // Get all elements with class="review" and hide them
  review = document.getElementsByClassName("review");
  for (i = 0; i < review.length; i++) {
    review[i].style.display = "none";
  }

  // Get all elements with class="review-link" and remove the class "active"
  reviewLink = document.getElementsByClassName("review-link");
  for (i = 0; i < reviewLink.length; i++) {
    reviewLink[i].className = reviewLink[i].className.replace(" active", "");
  }

  // Show the current tab, and add an "active" class to the button that opened the tab
  document.getElementById(tabs).style.display = "flex";
  evt.currentTarget.className += " active";
}
