import React, { useState } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import "./styles/header.scss";
import Logo from "../../assets/images/Logo.png";
import SearchIcon from "@material-ui/icons/Search";
import PersonOutlineIcon from "@material-ui/icons/PersonOutline";
import CartDropDown from "./cartDrop";
import Cart from "./cartIcon";
import {CartHiddenDrop} from '../../reducers/headerReducer/headerSelector'

// scroll function for sticky header
document.addEventListener("DOMContentLoaded", function () {
  window.onscroll = function () {
    StickyScroll();
  };

  var header = document.getElementById("myheader");
  var sticky = header.offsetTop;

  function StickyScroll() {
    if (window.pageYOffset > sticky) {
      header.classList.add("sticky");
    } else {
      header.classList.remove("sticky");
    }
  }
});
// end of scroll function*
const LogoHeader = ({ cartHidden }) => {
  //cart button at the top left used for opening  sidebar and closing
  var buttons = (
    <button
      className="header-button"
      onClick={() => {
        openNav();
        setbutton(
          (buttons = (
            <button
              className="header-button"
              onClick={() => {
                closeNav();
                setbutton(button);
              }}
            >
              x
            </button>
          ))
        );
      }}
    >
      &#9776;
    </button>
  );
  const [button, setbutton] = useState(buttons);
  {
    /* end of cart button*/
  }

  return (
    <>
      <div className="header" id="myheader">
        <Link to="/">
          <div href="#default" className="logo">
            <img src={Logo} />
          </div>
        </Link>
        {button}
        <div>
          <input type="text" className="header-searchInput" placeholder="Search for Produts" />
          <SearchIcon className="header-searchIcon" />
        </div>
        <Link to="/login-signup">
          <div>
            <PersonOutlineIcon className="header-icon" />
          </div>
        </Link>
        <Cart />
      </div>
      {cartHidden ? null : <CartDropDown />}
    </>
  );
};
const mapStateToProps = (state) => ({
  cartHidden: CartHiddenDrop(state)
});

export default connect(mapStateToProps)(LogoHeader);
// sidebar open and close fuction
function openNav() {
  document.getElementById("mySidenav").style.width = "250px";
  document.getElementById("main").style.marginLeft = "250px";
  document.getElementById("main").style.backgroundColor = "white";
}
// sidebar close fuction
function closeNav() {
  document.getElementById("mySidenav").style.width = "0";
  document.getElementById("main").style.marginLeft = "0";
  document.getElementById("main").style.backgroundColor = "white";
}
