import React from "react";
import { connect } from "react-redux";
import CartProductItem from "./cartProduct";
import "./styles/header.scss";
import { withRouter } from "react-router-dom";

import { selectCartItems } from "../../reducers/productReducer/productSelector";
import { toggleCartHidden,removeCartItems } from "../../reducers/actions/productActions";

const CartDropDown = ({ cartItems, removeCart, history,gotoViewCart }) => {
  return (
    <div className="cart-dropdown">
      <div className="cart-items">
        {cartItems.length
          ? cartItems.map((cartitem) => <CartProductItem key={cartitem.id} item={cartitem}  removeCart={ removeCart} />)
          :<div className='empty-message'>Your cart is empty</div>}
      </div>
      <button
        className="cart-dropdown-btn"
        onClick={() => {
          history.push("/my-cart");
         gotoViewCart()
        }}
      >
        Go Check Out
      </button>
    </div>
  );
};
const mapStateToProps = (state) => ({
  cartItems: selectCartItems(state),
});
const mapDispatchToProps = dispatch =>({
  removeCart: item => dispatch(removeCartItems(item)),
  gotoViewCart: ()=> dispatch(toggleCartHidden()),
})
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CartDropDown));
