import React from "react";
import { connect } from "react-redux";
import { removeCartItems, addCartItems, reduceCartItem } from "../../reducers/actions/productActions";

const ViewCartItem = ({ cartItem, removeCart, addCartItems,reduceCartItem }) => {
  const { name, image, quantity, price } = cartItem;
  const total = quantity * price;
  return (
    <div className="viewcart-item">
      <div className="image-container">
        <img src={image} />
      </div>
      <span className="name">{name}</span>
      <span className="price">{price}</span>
      <span className="quantity">
        <div className="arrow" onClick={() => reduceCartItem(cartItem)}>
          &#10094;
        </div>
        <span className="value">{quantity}</span>
        <div className="arrow" onClick={() => addCartItems(cartItem)}>&#10095;</div>
      </span>
      <span className="price">{total}</span>
      <span className="remove-button" onClick={() => removeCart(cartItem)}>
        &#10005;
      </span>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => ({
  removeCart: (item) => dispatch(removeCartItems(item)),
  addCartItems: (item) => dispatch(addCartItems(item)),
  reduceCartItem: (item) => dispatch(reduceCartItem(item)),
});

export default connect(null, mapDispatchToProps)(ViewCartItem);
