import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { connect } from "react-redux";
import {
  selectCartItems,
  selectCartItemsCount,
} from "../../reducers/productReducer/productSelector";
import {
  removeCartItems,
  addCartItems,
  reduceCartItem,
} from "../../reducers/actions/productActions";

const BasketHub = ({ cartItems, itemCount, removeCart, addCartItems, reduceCartItem }) => {
  return (
    <>
      <div className="baskethub">
        {cartItems.map((cartbasket) => {
          return (
            <div className="cartbasket-items" key={cartbasket.id}>
              <img src={cartbasket.image} alt="cart-items" className="cartbasket-img" />
              <div className="cartbasket-details">
                <p className="cartbasket-name">
                  {cartbasket.name}
                  <span className="cartbasket-remove">
                    <FontAwesomeIcon icon="trash-alt" onClick={() => removeCart(cartbasket)}/>
                  </span>
                </p>
                <p>Price: ₦{cartbasket.price}.00</p>
                <div className="cartbasket-addon">
                  <button className="cartbasket-plus" onClick={() => reduceCartItem(cartbasket)}>
                    -
                  </button>
                  <input className="cartbasket-qty" placeholder={cartbasket.quantity} />
                  <button className="cartbasket-plus" onClick={() => addCartItems(cartbasket)}>
                    +
                  </button>
                </div>
              </div>
            </div>
          );
        })}
      </div>
      {cartItems.length ? (
        <div>
          <p style={{ display: "flex", marginLeft: "2rem" }}>
            Subtotal: <span style={{ marginLeft: "8rem" }}>₦100,000,000.00</span>
          </p>
          <p style={{ display: "flex", marginLeft: "2rem", marginTop: ".1rem" }}>
            Total: <span style={{ marginLeft: "9.5rem" }}>₦100,150,000.00</span>
          </p>
          <div className="cartbasket-checkout-btn">
            <button>Procceed to Checkout</button>
          </div>
        </div>
      ) : null}
    </>
  );
};

const mapStateToProps = (state) => ({
  cartItems: selectCartItems(state),
  itemCount: selectCartItemsCount(state),
});
const mapDispatchToProps = (dispatch) => ({
  removeCart: (item) => dispatch(removeCartItems(item)),
  addCartItems: (item) => dispatch(addCartItems(item)),
  reduceCartItem: (item) => dispatch(reduceCartItem(item)),
});

export default connect(mapStateToProps, mapDispatchToProps)(BasketHub);
