import React from "react";
import { ConnectedRouter } from "connected-react-router";
import { Provider } from "react-redux";
import { Route, Switch } from "react-router-dom";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faStar,
  faSignOutAlt,
  faUserAlt,
  faDownload,
  faPaperPlane,
  faHome,
  faCreditCard,
  faShoppingBasket,
  faTachometerAlt,
  faClock,
  faTruck,
  faCartArrowDown,
  faMapMarkerAlt,
  faUser,
  faTh,
  faGripVertical,
  faThLarge,
  faGripLinesVertical,faTrashAlt
} from "@fortawesome/free-solid-svg-icons";
import "react-slideshow-image/dist/styles.css";
import "swiper/swiper-bundle.css";
import MainApp from "./components/main/MainApp";

import Layout from "./components/main/Layout";
import ProductDetail from "./components/MainBody/ProductDetail";
import DashBoard from "./components/dashboard/DashBoard";
import Auth from "./components/dashboard/Auth";
import TrackOrders from "./components/MainBody/TrackOrders";
import Shop from "./components/MainBody/Shop";
import configureStore,{history} from './store/index';
import ViewCartPage  from "./components/dashboard/ViewCartPage";
library.add(
  faStar,
  faSignOutAlt,
  faUserAlt,
  faDownload,
  faPaperPlane,
  faThLarge,
  faCartArrowDown,
  faMapMarkerAlt,
  faUser,
  faTh,
  faGripVertical,
  faGripLinesVertical,
  faHome,
  faCreditCard,
  faShoppingBasket,
  faTachometerAlt,
  faClock,
  faTruck,
  faTrashAlt
);

const store = configureStore();
function App(match) {
  return (
    <>
      <Provider store={store}>
        <ConnectedRouter history={history}>
          <Layout>
            <Switch>
              <div id="content">
                <Route exact={true} path="/" component={MainApp} />
                <Route path="/product/:id" component={ProductDetail} />
                <Route path="/my-account" component={DashBoard} />
                <Route path="/shop" component={Shop} />
                <Route path="/track-your-order" component={TrackOrders} />
                <Route path="/my-cart" component={ViewCartPage} />
              </div>
            </Switch>
          </Layout>
          <Route path={`${match.path}/login-signup`} component={Auth} />
        </ConnectedRouter>
      </Provider>
    </>
  );
}

export default App;
