import {
  ADD_MORE_BUTTON,
  SUB_MORE_BUTTON,
  ADD_ITEMS_TO_CART,
  REMOVE_CART_ITEM_BUTTON,
  REDUCE_CART_ITEM,
  PRODUCT_ITEM,
} from "../actions/actionTypes";
import SHOP_DATA from '../../components/MainBody/Data'
const INIT_STATE = {
  addmore: 1,
  cartItems: [],
  productData: SHOP_DATA
};

export const addItemsToCart = (cartItems, cartItemToAdd) => {
  const itemExist = cartItems.find((cartItem) => cartItem.id === cartItemToAdd.id);
  if (itemExist) {
    return cartItems.map((cartItem) =>
      cartItem.id === cartItemToAdd.id ? { ...cartItem, quantity: cartItem.quantity + 1 } : cartItem
    );
  }
  return [...cartItems, { ...cartItemToAdd, quantity: 1 }];
};

export const reduceCartItem = (cartItems, cartItemToReduce) => {
  const itemExist = cartItems.find((cartItem) => cartItem.id === cartItemToReduce.id);
  if (itemExist.quantity === 1) {
    return cartItems.filter((cartItem) => cartItem.id !== cartItemToReduce.id);
  }
  return cartItems.map((cartItem) =>
    cartItem.id === cartItemToReduce.id
      ? { ...cartItem, quantity: cartItem.quantity - 1 }
      : cartItem
  );
};

export default (state = INIT_STATE, action) => {
  const { type, payload } = action;
  switch (type) {
    case ADD_MORE_BUTTON:
      return { ...state, addmore: state.addmore + 1 };

    case SUB_MORE_BUTTON: {
      return { ...state, addmore: state.addmore - 1 };
    }

    case ADD_ITEMS_TO_CART:
      return {
        cartItems: addItemsToCart(state.cartItems, payload),
      };
    case REDUCE_CART_ITEM:
      return {
        cartItems: reduceCartItem(state.cartItems, payload),
      };
    case REMOVE_CART_ITEM_BUTTON:
      return {
        ...state,
        cartItems: state.cartItems.filter((cartItem) => cartItem.id !== payload.id),
      };
      case PRODUCT_ITEM:
        return{
          ...state,
        }
    default:
      return state;
  }
};
