import {
    TOGGLE_CART_HIDDEN,

} from '../actions/actionTypes';

const INIT_STATE = {
    cartHidden: true,
}

export default (state=INIT_STATE, action) =>{
    const {type, payload} = action;
    switch(type){
        case TOGGLE_CART_HIDDEN:
            return{
                ...state, cartHidden: !state.cartHidden
            }
        default:
            return state
    }
}